import React, {useEffect, useState} from 'react'
import {
  CButton,
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CCollapse,
  CDataTable,
  CListGroup,
  CListGroupItem,
  CModal,
  CModalBody,
  CModalFooter,
  CModalHeader,
  CRow,
  CSwitch
} from '@coreui/react'
import {toast, ToastContainer} from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import TextField from '@material-ui/core/TextField';
import {
  createRole,
  deleteRole,
  loadActions,
  loadRoleActions,
  loadRoles,
  updateDefinedActions,
  updateRole
} from '../../services/userManagementService'
import {useStore} from '../../MobxStore'

import {NotificationManager} from 'react-notifications';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Slide from '@material-ui/core/Slide';


const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

const fields = [
  {key: 'name', _classes: 'font-weight-bold'},
  {
    key: 'description',
    sorter: false,
    filter: false
  }, 'createdOn', {
    key: 'role_details',
    label: 'Details',
    sorter: false,
    filter: false
  }, {
    key: 'updates',
    label: '',
    sorter: false,
    filter: false
  },
  {
    key: 'deletes',
    label: '',
    sorter: false,
    filter: false
  }
];


const Roles = () => {

  const [open, setOpen] = React.useState(false);
  const [roleList, setRoles] = useState([]);
  const [modal, setModal] = useState(false);
  const [header, setHeader] = useState("");
  const [displayName, setDisplayName] = useState("");
  const [name, setName] = useState("");
  const [deleteName, setDeleteName] = useState("");
  const [description, setDescription] = useState("");
  const [actions, setActions] = useState([]);
  const [roleActionList, setRoleActionList] = useState([]);
  const [accordion, setAccordion] = useState(0);
  const store = useStore();


  const initialize = () => {
    loadRoles(store.login.token).then(response => {
      setRoles(response);
      console.log(response);
    }).catch(err => console.log(err));

    loadActions(store.login.token).then(response => {
      setActions(response);
      // console.log(" actions "+JSON.stringify(response));
    }).catch(err => console.log(err));
  };

  useEffect(() => {

    initialize();

  }, []);


  const load_roleActions = (item) => {
    let role_actions = [];
    new Promise((resolve, reject) => {
      loadRoleActions(store.login.token, item.id).then(response => {

        response[0].userActionList.forEach(function (entry) {
          role_actions.push(entry.id);
          console.log(entry.id);

        });
        resolve()
      }).catch(err => console.log(err));
    }).then(() => {
      console.log(" pushed " + role_actions);
      setRoleActionList(role_actions);
      toggle(item.id, item.name, item.description);

      console.log("role actions - - -" + role_actions);
    });


    //
  };


  const toggle = (header, name, description) => {
    setModal(!modal);
    setHeader(header);
    setName(name);
    setDisplayName(name);
    setDescription(description);

    if (header == "") {
      setRoleActionList([])
    }
  };

  const handletoggle = (id) => {
    let selected = roleActionList;
    new Promise((resolve, reject) => {
      const index = selected.indexOf(id);
      if (index > -1) {
        selected.splice(index, 1);
      } else {
        selected.push(id)
      }
      resolve()
    }).then(() => {
      console.log("zzzzzzzzzzzzzzzzzzzz " + selected);
      setRoleActionList(selected);

    });

    setRoleActionList([id]);

  };

  const handleRoleSubmit = (event) => {
    event.preventDefault();

    const roleUpdateDto = {
      "id": header,
      "name": name,
      "description": description
    };

    updateRole(store.login.token, roleUpdateDto).then(response => {
      if (response.status) {
        NotificationManager.success(response.message, 'Success', 3000);
        initialize();
        setDisplayName(name);
        // toggle("","","");

      } else {
        NotificationManager.error(response.message, 'Update Failed!', 3000);
      }
    })
      .catch(err => {
        console.log(err);
        if (err.response.status == 403) {
          NotificationManager.error("You are not allowed to perform this task", 'Update Failed!', 3000);
        } else {
          NotificationManager.error("Unable to try Role Update, check your connection", 'Update Failed!', 3000);
        }
      });

  };

  const handleActionSubmit = (event) => {
    event.preventDefault();

    const roleUpdateDto = {
      "userRoleId": header,
      "userActions": roleActionList,
    };

    updateDefinedActions(store.login.token, roleUpdateDto).then(response => {
      if (response.status) {
        NotificationManager.success(response.message, 'Success', 3000);
        initialize();

      } else {
        NotificationManager.error(response.message, 'Update Failed!', 3000);
      }
    })
      .catch(err => {
        console.log(err);
        if (err.response.status == 403) {
          NotificationManager.error("You are not allowed to perform this task", 'Update Failed!', 3000);
        } else {
          NotificationManager.error("Unable to try Update, check your connection", 'Update Failed!', 3000);
        }
      });

  };


  const handleRoleCreate = (event) => {
    event.preventDefault();

    if (name == "") {

      toast.error('Role Name Can not be empty!', {
        position: "top-center",
        autoClose: 3000,
        hideProgressBar: true,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
    } else {
      const roleInsertDto = {
        "userRoleName": name,
        "userRoleDescription": description,
        "userActions": roleActionList,
      };

      createRole(store.login.token, roleInsertDto).then(response => {
        if (response.status) {
          NotificationManager.success(response.message, 'Success', 3000);
          initialize();
          toggle("", "", "");

        } else {
          NotificationManager.error(response.message, 'Update Failed!', 3000);
        }
      })
        .catch(err => {
          console.log(err);
          if (err.response.status == 403) {
            NotificationManager.error("You are not allowed to perform this task", 'Update Failed!', 3000);
          } else {
            NotificationManager.error("Unable to try Update, check your connection", 'Update Failed!', 3000);
          }
        });

    }

  };

  const confirmDelete = (item) => {
    setOpen(true);
    setDeleteName(item);
  };

  const handleRoleDelete = (event, id) => {
    event.preventDefault();

    deleteRole(store.login.token, id).then(response => {
      if (response.status) {
        NotificationManager.success(response.message, 'Success', 3000);
        initialize();
        setOpen(false);
      } else {
        NotificationManager.error(response.message, 'Delete Failed!', 3000);
      }
    })
      .catch(err => {
        console.log(err);
        if (err.response.status == 403) {
          NotificationManager.error("You are not allowed to perform this task", 'Delete Failed!', 3000);
        } else {
          NotificationManager.error("Unable to try Delete, check your connection", 'Delete Failed!', 3000);
        }
      });


  };


  return (
    <>
      <ToastContainer/>

      <CModal
        show={modal}
        onClose={() => {
          toggle("", "", "")
        }}
        size='lg'
        backdrop
        closeOnBackdrop={false}
      >
        <CModalHeader size="modal-lg" closeButton><h4>
          <small>{header == 0 ? "Create User Role" : "Update User Role"}</small> - {header == 0 ? name : displayName}
        </h4></CModalHeader>
        <CModalBody>

          <form onSubmit={handleRoleSubmit}>
            <div class="form-row">
              <div class="col-md-5 mb-3">
                {/* <label for="validationDefault01">First name</label> */}
                <TextField id="outlined-basic" label="Name" onInput={e => setName(e.target.value)} value={name}
                           size="small" variant="outlined" required/>
              </div>
              <div class="col-md-5 mb-3">
                {/* <label for="validationDefault02">Last name</label> */}
                <TextField id="outlined-basic" label="Description" multiline rows={3}
                           onInput={e => setDescription(e.target.value)} value={description} size="small"
                           variant="outlined"/>
              </div>
              <div class="col-md-2 mb-3">
                {header == 0 ? "" :
                  <button type="submit" className="btn btn-outline-success m-2 btn-md">Update</button>}

              </div>
            </div>
          </form>
          <hr/>
          <CModalHeader size="modal-lg"><h4>
            <small>{header == 0 ? "Assign Actions for " : "Update Role Actions for "}</small> - {header == 0 ? name : displayName}
          </h4></CModalHeader>


          {actions.map((action, index) => {
            return (
              <>

                <div id="accordion">
                  <CCard className="mb-0">
                    <CCardHeader id="headingOne">
                      <CButton
                        block
                        color="link"
                        className="text-left"
                        // onClick={() => setAccordion(accordion === index ? null : index)}
                      >
                        <h5 className="m-0 p-0">{action.groupName}</h5>
                      </CButton>
                    </CCardHeader>
                    {/* <CCollapse show={accordion === index}> */}
                    <CCollapse show={accordion === 0}>
                      <CCardBody>
                        <CCard>


                          <CListGroup>
                            {
                              action.actionsDtos.map((act) => {

                                let enabled = false;
                                if (roleActionList.includes(act.userActionId)) {
                                  enabled = true
                                }

                                return (
                                  <>
                                    <CListGroupItem>
                                      <div className="d-flex justify-content-between">
                                        {act.action}
                                        <CSwitch className={'mx-1'} color={'success'}
                                                 labelOn={'\u2713'}
                                                 labelOff={'\u2715'}
                                                 checked={enabled}
                                                 onChange={
                                                   // (e)=>console.log(e.target.checked)
                                                   () => {
                                                     handletoggle(act.userActionId);
                                                   }
                                                   // ()=>{
                                                   //   setRoleActionList([1,2]);}
                                                 }

                                        />
                                      </div>

                                    </CListGroupItem>
                                  </>
                                );
                              })}

                          </CListGroup>

                        </CCard>

                      </CCardBody>
                    </CCollapse>
                  </CCard></div>


              </>
            )
          })}

        </CModalBody>
        {/* <CModalFooter /> */}
        <div className="d-flex justify-content-between">
          {header == 0 ?
            <button type="button" className="btn btn-outline-danger m-2 btn-md" onClick={() => {
              toggle("", "", "")
            }}>Cancel</button>
            : <button type="button" className="btn btn-outline-danger m-2 btn-md" onClick={() => {
              toggle("", "", "")
            }}>Close</button>}
          {header == 0 ?
            <button className="btn btn-outline-success m-2 btn-md" type="submit"
                    onClick={handleRoleCreate}>Submit</button> :
            <button className="btn btn-outline-success m-2 btn-md" onClick={handleActionSubmit}>Update</button>}


        </div>
        <CModalFooter/>
      </CModal>

      <CRow>
        <CCol xl={12}>
          <CCard>
            <CCardHeader>
              <div className="d-flex justify-content-between">
                <div><h4 style={{marginBottom: 2}}>Roles</h4>
                  <small className="text-muted"> user roles</small>
                </div>
                <div className="d-flex justify-content-end">
                  <button type="button" className="btn btn-outline-success m-2 btn-md" onClick={() => {
                    toggle(0, "", "")
                  }}>Add Role
                  </button>
                </div>
              </div>
            </CCardHeader>
            <CCardBody>
              <CDataTable
                items={roleList}
                fields={fields}
                hover
                tableFilter
                striped
                itemsPerPage={5}
                itemsPerPageSelect
                clickableRows
                // onRowClick={(item) => history.push(`/users/${item.id}`)}
                scopedSlots={{
                  'createdOn':
                    (item) => (
                      <td>

                        {
                          // new Date(item.createdOn).toLocaleDateString("en-US")
                          new Date(parseFloat(item.createdOn)).toLocaleString()
                        }

                      </td>
                    ),
                  'role_details':
                    (item, index) => {
                      return (
                        <td className="py-2">
                          <CButton
                            color="primary"
                            variant="outline"
                            shape="square"
                            size="sm"
                            // onClick={()=>{toggleDetails(index)}}
                          >
                            Detail
                          </CButton>
                        </td>
                      )
                    },

                  'updates':
                    (item, index) => {
                      return (
                        <td className="py-2">
                          <CButton
                            color="warning"
                            variant="outline"
                            shape="square"
                            size="sm"
                            disabled={item.name == "Admin"}
                            onClick={() => {

                              load_roleActions(item)
                            }}
                          >
                            Update
                          </CButton>
                        </td>
                      )
                    },
                  'deletes':
                    (item, index) => {
                      return (
                        <td className="py-2">
                          <CButton
                            color="danger"
                            variant="outline"
                            shape="square"
                            disabled={item.name == "Admin"}
                            size="sm"
                            onClick={
                              // (e)=>{handleRoleDelete(e,item.id)}
                              () => confirmDelete(item)
                            }
                          >
                            Delete
                          </CButton>
                        </td>
                      )
                    },
                }}
              />
              {/* <CPagination
              activePage={page}
              onActivePageChange={pageChange}
              pages={5}
              doubleArrows={false}
              align="center"
            /> */}
            </CCardBody>
          </CCard>
        </CCol>
      </CRow>

      <div>

        <Dialog
          open={open}
          TransitionComponent={Transition}
          keepMounted
          aria-labelledby="alert-dialog-slide-title"
          aria-describedby="alert-dialog-slide-description"
        >
          <DialogTitle id="alert-dialog-slide-title">{<h5>Are You sure?</h5>}</DialogTitle>
          <DialogContent>
            <DialogContentText id="alert-dialog-slide-description">
              {<div><h5 style={{textAlign: 'center'}}><small>You are about to delete
                role <strong>{deleteName.name}</strong></small></h5><h6 style={{textAlign: 'center'}}> Are You sure you
                want to proceed?</h6></div>}
            </DialogContentText>
          </DialogContent>
          <div className="d-flex justify-content-between">
            <button type="button" className="btn btn-outline-danger m-2 btn-sm" onClick={() => {
              setOpen(false)
            }}>Cancel
            </button>

            <button className="btn btn-outline-success m-2 btn-sm" type="submit" onClick={(e) => {
              handleRoleDelete(e, deleteName.id)
            }}>Continue
            </button>


          </div>
        </Dialog>
      </div>
    </>
  )
};

export default Roles
